@charset "utf-8";

/* Table */
table{border-collapse:collapse;border-spacing:0;background-color:#fff;
	caption{visibility:hidden;width:0;height:0;font-size:0}
	th{background:#eee;border:1px solid #bbb;padding:3px 0; letter-spacing: -0.8px}
	td{border:1px solid #d1d1d1;padding:3px 0 3px 5px; letter-spacing: -0.3px;
		a{color:#193d79;}
		&.path a{color:$color-path;}//링크색상
		&.center{text-align:center;padding-left:0;}
	}
	th, td{
		&.right{text-align:right;padding-right:10px;padding-left:0;}
	}
	tr.focus{background-color:$color-focus_line;}
}


/* Table_content */
.tab_contents_wrap{width:100%; margin-top:10px;border-top:none;
	h3{padding-bottom:5px;}

	table{
		tr td{position:relative;word-break: break-all;
			&:first-child{position:relative;}
			&:first-child:before{content:"";position:absolute; top:-1px; bottom:-1px;left:-2px; width:2px; background-color:none;}
		}
		tr:hover td{background-color:$color-hover_line;}
		tr.focus:hover td{background-color:$color-focus_line;}
	}
}


//TR
.tab_contents_wrap table tr{
	&.off,
	&.off2,
	&.off3,
	&.off4,
	&.off5,
	&.off6,
	&.off7,
	&.off8,
	&.off9{display: none;}

	//삭제
	&.del{
		> td{background-color:$color-del_light; color: #bbb}
		> td:first-child:before{background-color:#999;}
		> td a{color: #bbb}
	}

	//동일
	// &.equal td a{color: $color-black}
	&.equal{
		// > td.path a{color:#333;}
	}
	
	//2차Open
	&.open2{
		td{color:#888}
		td.path.no-link > a {color: #888;}
		&.open2-off {display: none;}
	}

	//netive
	&.netive{
		& > *{color:#999;}
		&  td{text-decoration: line-through;}
		& > td.path a{color:#999;}
	}
}

//TD
.tab_contents_wrap table td{
	&.num{padding-right:4px;padding-left:0;text-align:right;
		.number a{text-decoration: none;cursor:default;}
	}
	&.info{font-size:10px;
		>span{display:inline-block;padding:2px 5px;margin:5px 0 0 5px;background:#fff;border-radius:4px;border:1px solid #d1d1d1;
			&:hover{text-decoration:underline;cursor: pointer;}
		}
	}
	&.rdate,
	&.mdate,
	&.plan{text-align:center;}
	&.path.no-link > a {color: #000;text-decoration: none;cursor: default;}
	&.ddate,&.rdate, &.mdate,&.corder, &.plan, &.design{text-align: center;padding: 3px 0 3px 0px;}/* 예상일 */
	&.etc{position:relative;padding-right:15px; min-height:15px;
		.desc{min-height:15px;}
	}
	&.pdate, &.mdate{text-align: center;}
}

body[data-childOption="true"] tr.child td.num{color:#aaa;
	a{color:#aaa;}
}







