/* Table_content */
.tab_contents_wrap{
	//화면 ID 두글자로 분리하여 가독성 높이기 위한...(2021.1222 : ing)
	// @ JS : Etc.pathSplit();
	table tr{
		&:hover td,
		&.focus td{
			&.path > a .str2{
				color: #0075ff; //text-shadow: 2px 2px 13px rgba(0,0,0,0.2);
			}
		}
	}
}


