// info_section
//-----------------------
.btn_fold_info{position:relative;text-align:right;
	a{z-index:8; position:absolute;top:-1px;right:10px;display:inline-block;width:60px;height:22px;line-height:20px;text-align:center; text-decoration:none; font-weight:bold; background-color:#f6f6f6;color:#999; border-bottom-left-radius:5px;border-bottom-right-radius:5px; border:1px solid #ddd;border-top:none; }
}

body.headerFloating {
	.btn_fold_info a{/*  right:10px;background-color:#f6f6f6; */ border:1px solid #ddd;}
	.tab_nav{border-bottom:1px solid #f6f6f6;}
}

body[data-headerclose="false"] .btn_fold_info a{border-top:none;}



// table_content 
//-----------------------
.btn_fold_depth{position: relative;display: inline-block;width: 9px; height:9px; background-color: #999; border-radius: 50%; margin-top:2px; margin-right: 2px;text-decoration: none;
	i{position: absolute; left: 0px; top: 4px;  display: block; width:9px; height: 1px;background-color: #ddd;
		&:before{content:''; display: block; position: absolute; left: 4px; top: -4px; width:1px; height: 9px;  margin-left: 0px;background-color: #ddd;}
	}
	&.on{color: #ddd;transition: all ease-in-out 0.26s; background-color: #ddd;
		i{background-color: #fff;
			&:before{display: none;}
		}
	}
	h3 &{float: none !important;vertical-align: top;margin-right: 3px;margin-top: 3px;
		// &.on{background-color: #999}
	}
	th &.on{color: #bbb; background-color: #bbb;}
	th &:hover, &:hover{color:#666; background-color: #666;}
}


// Log 
.btn_log{position:absolute; top:50%; margin-top: -6px;right:5px;width: 11px;height: 11px;line-height: 0;background-color: #ccc;color:#ccc;border-radius:50%;
	i{display: block; background: url($svg-arrow) 0 0 no-repeat;width: 100%; height:100%; }
	&.on{right:5px;opacity:1; transition: all  ease-in-out 0.26s}
	&.on:hover{color:#666;background-color: #666; }
}



//pop_area , Setting 
//-----------------------
.btn_close{padding:4px; border-radius:50%;background-color:#fff;transition: all  ease-in-out 0.26s;
	& i{display: block;width: 11px; height: 11px; background:url($svg_close) 0 0 no-repeat;}
	&:hover{background-color:#bbb;
		i{@include background($svg_close, '%23666', '#fff', 0, 0);
			transition: all ease-in-out 0.2s;  transform:rotate(360deg); color:#333; 
		}
	}
}


