@charset "utf-8";



// Info_section
.info_section{z-index:5;position:relative;@extend .clearfix; padding:0px 10px 10px; background-color:#f6f6f6; border:1px solid #efefef;border-top:none;
	h2{padding:5px 0;}

	.box{float:left;padding-right:10px;
		.box_wrap{display: inline-block;  margin-right: 10px;}
		&.hide .box_wrap{min-height:auto;display:none; }
	}

	table td{background-color:#fff;padding:3px  5px;}

	// 검색
	#id_search{width:98%}

	//open, close
	//2021.1222 : 사용하지 않음
	.btn_fixed_info{position:absolute; right:0px;top:0px; 
		i{display: block; background: url($svg-lock) 50% 50% no-repeat;width: 100%; height:100%; background-size: 100%; width: 40px; height: 40px; background-size: 25px; transition: all  ease-in-out 0.1s;}
		&.on{color:#bbb;
			.fa-thumbtack{display: block; background: url($svg-lock-over) 50% 50% no-repeat;background-size: 20px;}
		}
	}

	.btn_close_info{display:inline-block;min-width:10px;padding:1px 2px 2px;color:#808080;border:1px solid #dedede; text-align:center; font-size:12px;line-height:11px; text-decoration:none; border-radius:5px;}
}



// info_box
//-----------------------
.info_box{
	.history_area{width:95%; height:49px;overflow-y:auto; margin:3px 0 0;padding:4px;font-size:11px; color:#999; border:1px solid #ccc;}
}
html.ie .info_section .info_box .history_area{height: 45px}


// link_box
//-----------------------
 .link_box{}


// filter_box
//-----------------------
.filter_box{ display: none;
	.filterOption {
		p {display:inline-block;}
		p strong{display:inline-block;line-height:22px;}
		p label{display:inline-block;line-height:22px; margin-left:5px;}
		p input{display:inline-block;vertical-align:text-bottom;line-height:22px;}
	}
}



// search_box
//-----------------------
//Sorting : search_select
.search_box{
	.search_select_area{
		label{margin-right: 3px; display: none;}
		select{height: 22px;padding-bottom: 2px;border:1px solid #bbb; border-radius:4px; min-width:80px; font-size:11px;letter-spacing: -0.8px;line-height: 1.5 ; outline: 0 ;color: #000;}
	}
}

.tab_contents_wrap{
	.search_select_area{position: relative;
		label{display: none;}
		select{height: auto;min-width: auto; border: none; background: #eee; font-weight: bold; font-size:11px;letter-spacing: -0.8px;line-height: 1.5 ; outline: 0 ;color: #000;}
		.search_cnt {position:absolute; top:-18px; right:-10px; min-width: 20px; padding: 2px 2px;background-color: #efefef; border-radius: 8px; font-size: 10px; box-shadow: -1px 2px 3px 2px rgba(0, 0, 0, .14), 0 1px 0px 0px rgba(#000, 0.12); }
	}
}




// comp_box
//-----------------------
.comp_box{
	.box_wrap{ display: inline-block;
		> span{display:inline-block;padding:2px 5px; margin:0 5px 5px 0;background:#fff;border-radius:4px;border:1px solid #d1d1d1;
			&:hover{text-decoration:underline;cursor: pointer;}
		}
	}
}

