@charset "utf-8";

//dimmed
.dimmed{z-index:998;display:none;position:fixed;left:0; right:0; top:0;bottom:0;background-color:#000; opacity:0.5;}

// popUp
body.is_popOn{
	.dimmed{display:block;}
	.modal{display:block;  top:3%; opacity:0; }
	.modal.motion{top:0;transition: all  ease-in-out 0.2s;opacity:1;}
	.modal.motion_end{top:-3%;transition: all  ease-in-out 0.2s;opacity:0;}
}

.modal{z-index:1050; display:none;position:fixed; left: 0;top: 0;bottom: 0;right: 0; pointer-events: none; 
	.pop_area{ position: relative; width: auto; margin: 0 auto; height: 100vh;display: flex; align-items: center; 
		.pop_wrap{position:relative;min-width:360px; max-width:calc(100vw - 80px); margin: 0 auto; background-color: #fff;  border-radius:6px;  pointer-events: auto;}

		.pop_head{position:relative;padding:10px 15px; padding-right:25px; font-size:16px; font-weight:bold;color:#333;line-height:17px;}
		.pop_con{padding:10px 15px 30px;max-height:calc(100vh - 200px);overflow-y:auto;
			.desc{position:relative;display:block;padding-left:5px;font-size:12px}
			.desc:after{content:''; position:absolute; top:8px; left:0; width:2px;height:2px; background-color:#333;}
		}
		.btn_close{z-index:10;position:absolute;right:10px;top:10px;}
	}
}


//Excel Export
.table_excel{position:relative; min-width:1400px; padding-top:30px;
	.btn_copy{position: absolute; z-index: 9999; top: -0px; right: 0;}
	.table_excel_head{z-index:99;position:absolute; top:30px; left:0; right:17px;
		table.fixed{width: 100%;
			thead th{
				label{display: inline-block;vertical-align: top; margin-top: 2px;}
				label span{display:inline-block;padding-left: 5px; vertical-align:top; margin-top:-1px;}
				input[type="checkbox"]{}
			}
		}
	}

	.table_excel_body{position:relative;max-height:calc(100vh - 260px);margin-top:0; padding-top:23px; overflow-y:auto; }
}