@charset "utf-8";



// Responsive
/* ----------- */
/* 모바일 hidden */
html.mobile{
	body #wrap #content{padding-left:3px; padding-right:3px;}
	h1{font-size:18px;}

	.info_section .info-box{display:none;}

	.tab_contents_wrap{
		/*.depth3,*/
		.depth4,
		.depth5,
		/*.depth6,*/
		.plan,
		.design,
		.corder,
		/*.test,*/
		.ddate,
		.info{display:none;}

		/*pc px > % 로 변경*/
		.num{width:3% !important;text-align:center;}
		.depth2{width:9% !important;}
		.depth3{width: !important;}
		.depth4{width: !important;}
		.depth5{width: !important;}
		.depth6{width: !important;}
		.path{width:5% !important;}
		.plan{width: !important;}
		.design{width: !important;}
		.corder{width: 6%!important;}
		.ddate{width: !important;}
		.rdate{width: !important;}
		.mdate{width: !important;}
		.test{width: 50px !important;}
		.info{width: !important;}
		.etc{width: !important;}

		table tr th,
		table tr td{padding-left:1px; padding-right:1px;font-size:10px; word-break: initial;}
	}
}




// 반응형 그리드
@media (max-width: 820px) {
	.coder
	, .pid {display:none;}
}
@media (max-width: 720px) {
	.rdate
	, .pdate
	, .mdate
	, .etc{display:none;}
}
@media (max-width: 620px) {
	.coder
	, .rdate
	, .pdate
	, .mdate
	, .etc
	, .pid {display:none;}
	.header .tab_nav>ul li a {padding:4px;font-size:12px;}
}

